import React, { useContext, useState, useEffect } from 'react'
import generator from 'generate-password'
import { UserContext } from '../context/UserContext'
import { LangContext } from '../context/LangContext'
import GeoAPI from '../utils/GeoAPI'

import passImage from '../assets/password.png'
import clipboardImg from '../assets/clipboard.png'
import dropImg from '../assets/down-arrow.png'

const geoApi = new GeoAPI()

function FarmerForm() {
    const context = useContext(UserContext)
    const langContext = useContext(LangContext)

    const [location, setLocation] = useState({
        country: { countries: [], drop: false }, 
        state: { states: [], drop: false }, 
        city: { cities: [], drop: false }
    })

    useEffect(() => {
        fetchCountries()
        langContext.textUpdate({ value: "English", id: "en" })
    },[])

    const errors = context.errors
    const texts = langContext.texts

    const onSubmit = event => {
        event.preventDefault()
        context.saveUserData()
    }

    const fetchCountries = async () => {
        let result = await geoApi.getCountries()
        if(result.success === true) {
            setLocation({ ...location, country: { ...location.country, countries: result.data} })
        }
    }

    const generatePassword = () => {
        let genPassword = generator.generate({ length: 14, numbers: true })
        context.setUserData({ ...context.userData, password: genPassword, })
    }

    const addCountry = async ({ value, id }) => {
        setLocation({ ...location, country: { ...location.country, drop: false } })
        context.setUserData({ ...context.userData, country: value })
        context.setErrors({})
        geoApi.countryID = id

        let result = await geoApi.getStates()
        if(result.success === true) {
            setLocation({ ...location, 
                state: { ...location.state, states: result.data }, 
                country: { ...location.country, drop: false }  
            })
        }
    }

    const openStateField = () => {
        if(location.state.states.length === 0) {
            context.setErrors({...context.errors, state: "Please select country first"})
            return
        }
        setLocation({ ...location, state: { ...location.state, drop: !location.state.drop } })
    }

    const addState = async ({ value, id }) => {
        setLocation({ ...location, state: { ...location.state, drop: false } })
        context.setUserData({ ...context.userData, state: value })
        context.setErrors({})
        geoApi.stateID = id

        let result = await geoApi.getCities()
        if(result.success === true) {
            setLocation({ ...location, 
                city: { ...location.city, cities: result.data }, 
                state: { ...location.state, drop: false }  
            })
        }
    }

    const openCityField = () => {
        if(!geoApi.stateID || !geoApi.countryID) {
            context.setErrors({...context.errors, district: "Please select country and state first"})
            return
        }
        setLocation({ ...location, city: { ...location.city, drop: !location.city.drop } })
    }

    const addDistrict = async ({ value, id }) => {
        setLocation({ ...location, city: { ...location.city, drop: false } })
        context.setUserData({ ...context.userData, district: value })
    }

    return (
        <form onSubmit={onSubmit} autoComplete="new-password">
            <div className="form-row">
                <div className="form-group col-md-6">
                    <label>{texts.firstName}</label>
                    <input type="text" autoComplete="off"
                        className={`form-control ${errors.firstName && 'is-invalid'}`} 
                        name="firstName"
                        placeholder={texts.firstName}
                        value={context.userData.firstName}
                        onChange={context.onChange} 
                    />
                    {errors.firstName && (
                        <div className="invalid-feedback">
                            { errors.firstName }
                        </div>
                    )}
                </div>

                <div className="form-group col-md-6">
                    <label>{texts.lastName}</label>
                    <input type="text"
                        className={`form-control ${errors.lastName && 'is-invalid'}`}  
                        name="lastName"
                        placeholder={texts.lastName}
                        value={context.userData.lastName}
                        onChange={context.onChange} 
                    />
                    {errors.lastName && (
                        <div className="invalid-feedback">
                            { errors.firstName }
                        </div>
                    )}
                </div>
            </div>

            <div className="form-row">
                <div className="form-group col-md-6">
                    <label>{texts.guardian}</label>
                    <input type="text"
                        className={`form-control ${errors.guardian && 'is-invalid'}`}  
                        name="guardian"
                        placeholder={texts.guardian}
                        value={context.userData.guardian}
                        onChange={context.onChange} 
                    />
                    {errors.guardian && (
                        <div className="invalid-feedback">
                            { errors.guardian }
                        </div>
                    )}
                </div>

                <div className="form-group col-md-6">
                    <label>{texts.aadhar}</label>
                    <input type="text"  
                        className={`form-control ${errors.aadharCard && 'is-invalid'}`}  
                        name="aadharCard"
                        placeholder={texts.aadhar}
                        value={context.userData.aadharCard}
                        onChange={context.onChange} 
                    />
                    {errors.aadharCard && (
                        <div className="invalid-feedback">
                            { errors.aadharCard }
                        </div>
                    )}
                </div>
            </div>

            <div className="form-group">
                <label>{texts.address}</label>
                <input type="text"
                    className={`form-control ${errors.street && 'is-invalid'}`}  
                    name="street"
                    placeholder={texts.address}
                    value={context.userData.street}
                    onChange={context.onChange}
                />
                {errors.street && (
                    <div className="invalid-feedback">
                        { errors.street }
                    </div>
                )}
            </div>

            <div className="form-row">
                <div className="form-group col-md-6">
                    <label>{texts.country}</label>

                    <div className="btn-group bg-white dropup-02 d-block">
                        <button type="button" onClick={() => setLocation({ ...location, country: { ...location.country, drop: !location.country.drop } })}
                            className={`btn is-invalid custom-drop d-flex align-items-center justify-content-between ${location.country.drop && ' custom-drop-none'}`}>
                            <span>
                                { context.userData.country ? <span style={{ color: 'black' }}>{context.userData.country}</span> : texts.country}
                            </span>
                            <img src={dropImg} alt="drop" 
                                style={{ maxHeight: '0.8rem', width: 'auto'}} />
                        </button>
                        <ul className={`suggestionCard bg-red ${ !location.country.drop && 'd-none'}`}>
                            { location.country.countries.map((country, i) => <li onClick={() => addCountry({ value: country.value, id: country.id})}
                                key={country.id} className="suggestionItem">{ country.value }</li>)}
                        </ul>
                        {errors.country && (
                            <div className="invalid-feedback">
                                { errors.country }
                            </div>
                        )}
                    </div>
                </div>

                <div className="form-group col-md-6">
                    <label>{texts.state}</label>

                    <div className="btn-group bg-white dropup-02 d-block">
                        <button type="button" onClick={() => openStateField()}
                            className={`btn is-invalid custom-drop d-flex align-items-center justify-content-between ${location.state.drop && ' custom-drop-none'}`}>
                            <span>
                                { context.userData.state ? <span style={{ color: 'black' }}>{context.userData.state}</span> : texts.state}
                            </span>
                            <img src={dropImg} alt="drop" 
                                style={{ maxHeight: '0.8rem', width: 'auto'}} />
                        </button>
                        <ul className={`suggestionCard bg-red ${ !location.state.drop && 'd-none'}`}>
                            { location.state.states.map((state, i) => <li onClick={() => addState({ value: state.value, id: state.id })}
                                key={state.id} className="suggestionItem">{ state.value }</li>)}
                        </ul>
                        {errors.state && (
                            <div className="invalid-feedback">
                                { errors.state }
                            </div>
                        )}
                    </div>
                </div>
            </div>

            <div className="form-row">
                <div className="form-group col-md-6">
                    <label>{texts.village}</label>
                    <input type="text"
                        className="form-control" 
                        name="village"
                        placeholder={texts.village}
                        value={context.userData.village}
                        onChange={context.onChange} 
                    />
                </div>

                <div className="form-group col-md-6">
                    <label>{texts.taluka}</label>
                    <input type="text"
                        className="form-control" 
                        name="taluka"
                        placeholder={texts.taluka}
                        value={context.userData.taluka}
                        onChange={context.onChange} 
                    />
                </div>
            </div>

            <div className="form-row">
                <div className="form-group col-md-6">
                    <label>{texts.district}</label>

                    <div className="btn-group bg-white dropup-02 d-block">
                        <button type="button" onClick={() => openCityField()}
                            className={`btn is-invalid custom-drop d-flex align-items-center justify-content-between ${location.city.drop && ' custom-drop-none'}`}>
                            <span>
                                { context.userData.district ? <span style={{ color: 'black' }}>{context.userData.district}</span> : texts.district}
                            </span>
                            <img src={dropImg} alt="drop" 
                                style={{ maxHeight: '0.8rem', width: 'auto'}} />
                        </button>
                        <ul className={`suggestionCard bg-red ${ !location.city.drop && 'd-none'}`}>
                            { location.city.cities.map((city, i) => <li onClick={() => addDistrict({ value: city.value, id: city.id })}
                                key={city.id} className="suggestionItem">{ city.value }</li>)}
                        </ul>
                        {errors.district && (
                            <div className="invalid-feedback">
                                { errors.district }
                            </div>
                        )}
                    </div>
                </div>

                <div className="form-group col-md-6">
                    <label>{texts.pincode}</label>
                    <input type="text"
                        className={`form-control ${errors.pincode && 'is-invalid'}`} 
                        name="pincode"
                        placeholder={texts.pincode}
                        value={context.userData.pincode}
                        onChange={context.onChange} 
                    />
                    {errors.pincode && (
                        <div className="invalid-feedback">
                            { errors.pincode }
                        </div>
                    )}
                </div>
            </div>
            
            <div className="form-row">
                <div className="form-group col-md-6">
                    <label>{texts.phoneNumber}</label>
                    <input type="text"
                        className={`form-control ${errors.phone && 'is-invalid'}`} 
                        name="phone"
                        placeholder={texts.phoneNumber}
                        value={context.userData.phone}
                        onChange={context.onChange} 
                    />
                    {errors.phone && (
                        <div className="invalid-feedback">
                            { errors.phone }
                        </div>
                    )}
                </div>

                <div className="form-group col-md-6">
                    <label>{texts.whatsapp}</label>
                    <input type="text"
                        className={`form-control ${errors.whatsapp && 'is-invalid'}`}
                        name="whatsapp"
                        placeholder={texts.whatsapp}
                        value={context.userData.whatsapp}
                        onChange={context.onChange} 
                    />
                    {errors.whatsapp && (
                        <div className="invalid-feedback">
                            { errors.whatsapp }
                        </div>
                    )}
                </div>
            </div>

            <div className="form-row">
                <div className="form-group col-md-6">
                    <label>{texts.telegram}</label>
                    <input type="text"
                        className={`form-control ${errors.telegram && 'is-invalid'}`}
                        name="telegram"
                        placeholder={texts.telegram}
                        value={context.userData.telegram}
                        onChange={context.onChange} 
                    />
                    {errors.telegram && (
                        <div className="invalid-feedback">
                            { errors.telegram }
                        </div>
                    )}
                </div>

                <div className="form-group col-md-6">
                    <label>{texts.emailAddress}</label>
                    <input type="text"
                        name="email"
                        className={`form-control ${errors.email && 'is-invalid'}`} 
                        placeholder={texts.emailAddress}
                        value={context.userData.email}
                        onChange={context.onChange} 
                    />
                    {errors.email && (
                        <div className="invalid-feedback">
                            { errors.email }
                        </div>
                    )}
                </div>
            </div>

            <div className={`form-group ${!context.userData._id && 'd-none '} d-flex align-items-end justify-content-between`}>
                <div className="d-flex flex-column" style={{ flex: '1' }}>
                    <label>{texts.password}</label>
                    <input type="text"
                        name="password"
                        className={`form-control ${errors.password && 'is-invalid'}`}
                        placeholder={texts.password}
                        value={context.userData.password}
                        onChange={context.onChange} 
                    />
                    {errors.password && (
                        <div className="invalid-feedback">
                            { errors.password }
                        </div>
                    )}
                </div>
                <div className="d-flex ml-4">
                    <div className="mr-3">
                        <label>{texts.generate}</label>
                        <button type="button" onClick={() => generatePassword()}
                            className="btn bg-success d-flex align-items-end justify-content-center shadow-sm" 
                            style={{ maxHeight: '2.4rem'}}>
                            <img style={{ height: '1.36rem', width: 'auto' }}
                                src={passImage} alt="generate password." />
                        </button>
                    </div>
                    <div>
                        <label>{texts.copy}</label>
                        <button type="button" 
                            onClick={() => {navigator.clipboard.writeText(context.userData.password)}}
                            className="btn bg-white d-flex align-items-end justify-content-center shadow-sm copy-btn" 
                            style={{ maxHeight: '2.4rem'}}>
                            <img style={{ height: '1.36rem', width: 'auto' }}
                                src={clipboardImg} alt="copy password." />
                        </button>
                    </div>
                </div>
            </div>

            <button type="submit" className="btn btn-success btn-block mt-4">
                <div className={`spinner-border spinner-border-sm text-light ${!context.loading && 'd-none'}`} 
                    role="status" >
                    <span className="sr-only">Loading...</span>
                </div>
                <span className={`${context.loading && 'd-none'}`}>
                    { context.userData._id ? 'Update' : texts.submit }
                </span>
            </button>
        </form>
    )
}

export default FarmerForm;
