const axios = require('axios')

class GeoAPI {
    constructor() {
        this.stateID = ''
        this.countryID = ''
    }

    async extricate(url) {
        const response = await axios.get(url)
        if(response.data.status === 'success') {
        let results = []
        for (const [key, value] of Object.entries(response.data.result)) results.push({ id: key, value })
            return this.result(true, results, "Successful operation!"  )
        }
        return this.result(false, [], "Something went wrong!"  )
    }

    async getCountries() {
        let url = 'https://geodata.solutions/api/api.php?type=getCountries' 
        return await this.extricate(url)
    }

    async getStates() {
        if(!this.countryID) {
            return this.result(false, [], "Please select country first")
        }
        let url = `https://geodata.solutions/api/api.php?type=getStates&countryId=${this.countryID}`
        return await this.extricate(url)
    }

    async getCities() {
        if(!this.countryID || !this.stateID) {
            return this.result(false, [], "Please select country and state first")
        }
        let url = `https://geodata.solutions/api/api.php?type=getCities&countryId=${this.countryID}&stateId=${this.stateID}`
        return await this.extricate(url)
    }

    result(success, data, message) {
        return { success, data, message }
    }
}

export default GeoAPI