import React, { createContext, useState } from 'react'
import axios from 'axios'
import formLang from '../utils/formLang'
export const LangContext = createContext()

const LangContextProvider = (props) => {
    const [currentLang, setCurrentLang] = useState({ value: 'English', id : 'en' })
    const [texts, setTexts] = useState(formLang)
    const [langLoad, setLangLoad] = useState(false)

    const textUpdate = async ({ value, id }) => {
        setLangLoad(true)
        setCurrentLang({ value, id })
        try {
            let response = await axios.get(`/api/language/${id}`)
            setTexts(response.data)
            setLangLoad(false)
        } catch (err) {
            console.log(err)
            setLangLoad(false)
            return texts
        }
    }
   
    return (
        <LangContext.Provider value={{ textUpdate, texts, currentLang, langLoad }}>
            {props.children}
        </LangContext.Provider>
    )
}

export default LangContextProvider