import React, { useEffect, useContext, useState } from 'react'
import { Link } from 'react-router-dom'
import { UserContext } from '../context/UserContext'
import { LangContext } from '../context/LangContext'
import langList from '../utils/langList'

import './Formpage.css'
import FarmerForm from '../components/FarmerForm'
import leftArrow from '../assets/left-arrow.png'
import dropImg from '../assets/down-arrow.png'
import loadani from '../assets/loadani.gif'

function Formpage(props) {
  const context = useContext(UserContext)
  const langContext = useContext(LangContext)
  const [showDrop, setShowDrop] = useState(false)
  let texts = langContext.texts

  useEffect(() => {
    if(!context.tempToken.token && !context.userData._id) props.history.push('/')
  },[])

  const updateLanguage = ({ value, id }) => {
    langContext.textUpdate({ value, id })
    setShowDrop(false)
  }  

  return (
    <div className="container mb-5">
        <div className="d-flex justify-content-between align-items-center mt-3">
          <div style={{ fontSize: '2rem', fontWeight: '300'}} className="text-success">{texts.form}</div>
          <span onClick={() => props.history.push('/')} style={{ cursor: 'pointer' }}>
            <img style={{ height: '2.7rem', width: 'auto' }}
              src={leftArrow} alt="back" />
          </span>
        </div>
        <div className="row my-2">
            <div className="col">

              <div className="form-group p-0">
                <label>Select Language</label>
                <div className="btn-group lang-drop dropup-02 d-block">
                    <button type="button" onClick={() => setShowDrop(!showDrop)}
                        className={`btn is-invalid custom-drop d-flex align-items-center justify-content-between ${showDrop && ' custom-drop-none'}`}>
                        <span>
                          { langContext.currentLang.value }
                        </span>
                        { !langContext.langLoad && (
                          <img src={dropImg} alt="drop" 
                            style={{ maxHeight: '0.8rem', width: 'auto'}} />
                        )}
                        { langContext.langLoad && (
                          <img src={loadani} alt="drop" 
                            style={{ maxHeight: '1.4rem', width: 'auto'}} />
                        )}
                    </button>
                    <ul className={`suggestionCard ${!showDrop && 'd-none'}`}>
                        { langList.map((lang, i) => <li onClick={() => updateLanguage({ value: lang.value, id: lang.id }) }
                            key={lang.id} className="suggestionItem">{ lang.value }</li>)}
                    </ul>
                </div>
              </div>

              <div className="px-4 py-3 form-contaier shadow-sm">
                <FarmerForm />
              </div>
            </div>
            
            <div className="col d-flex justify-content-end align-items-start">
              { context.userData._id && (
                <div style={{marginTop: 40 + '%'}}>
                  <span className="display-4 text-success">{texts.submit}</span>
                  <div className="text-center sub-text">new form click here</div>
                  <div className="text-center mt-3">
                    <span style={{ cursor: 'pointer' }}
                      className="text-success" onClick={() => context.setNewUser()}>New form</span>
                  </div>
                </div>
              )}
              { !context.userData._id && (
                <div style={{marginTop: 40 + '%'}}>
                  <span className="display-4 text-success">{texts.existing}</span>
              <div className="text-center sub-text">{texts.userClickHere}</div>
                  <div className="text-center mt-3">
                    <Link className="text-success"
                      to={{
                          pathname: "/"
                      }}>
                        {texts.login}
                    </Link>
                  </div>
                </div>
              )}
            </div>
        </div>
    </div>
  )
}

export default Formpage;
